import React from 'react';
import './App.css';
import Clarifai from 'clarifai';
import Particles from 'react-particles-js';
import ImageLinkForm from './components/imageLinkForms/ImageLinkForm';
import Logo from './components/logos/Logo';
import Navigation from './components/navigations/Navigation';
import Rank from './components/rank/Rank';
import ImageRecognize from './components/imageRecognize/ImageRecognize';
import Sigin from './components/sigin/Sigin';
import Register from './components/register/Register';

const particleOptions = {
  particles: {
    number: {
      value: 160,

      density: {
        enable: false,
      },
    },
    shape: {
      type: 'star',
    },
    size: {
      value: 10,
      random: true,
    },
    move: {
      direction: 'bottom',
      out_mode: 'out',
    },
    line_linked: {
      enable: false,
    },
  },
  interactivity: {
    events: {
      onclick: {
        enable: true,
        mode: 'remove',
      },
    },
    modes: {
      remove: {
        particles_nb: 10,
      },
    },
  },
};

const app = new Clarifai.App({
  apiKey: '8073f5499ca744c185be57c86259e2aa',
});
class App extends React.Component {
  state = {
    input: '',
    imageURL: '',
    box: {},
    route: 'sigin',
    isSigin: false,
  };

  onInputChange = (event) => {
    this.setState({ input: event.target.value });
  };
  calculateFaceLocation = (data) => {
    const clarifaiFace =
      data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputimage');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - clarifaiFace.right_col * width,
      bottomRow: height - clarifaiFace.bottom_row * height,
    };
  };

  displayFaceBox = (box) => {
    this.setState({ box: box });
  };
  onButtonClick = () => {
    this.setState({ imageURL: this.state.input });

    app.models
      .predict('c0c0ac362b03416da06ab3fa36fb58e3', this.state.input)
      .then((response) =>
        this.displayFaceBox(this.calculateFaceLocation(response))
      )
      .catch((err) => console.log(err));
  };
  onRouteChange = (route) => {
    if (route === 'signout') {
      this.setState({ isSigin: false });
    } else if (route === 'home') {
      this.setState({ isSigin: true });
    }
    this.setState({ route });
  };
  render() {
    const { isSigin, route, box, imageURL } = this.state;
    return (
      <div className='App'>
        <Particles className='particles' params={particleOptions} />
        <Navigation isSigin={isSigin} onRouteChange={this.onRouteChange} />

        {route === 'home' ? (
          <div>
            <Logo />
            <Rank />
            <ImageLinkForm
              InputChange={this.onInputChange}
              onButtonClick={this.onButtonClick}
            />
            <ImageRecognize box={box} imageURL={imageURL} />
          </div>
        ) : route === 'sigin' ? (
          <Register onRouteChange={this.onRouteChange} />
        ) : (
          <Sigin onRouteChange={this.onRouteChange} />
        )}
      </div>
    );
  }
}

//<Register onRouteChange={this.onRouteChange} />
export default App;
